#app {
    max-width: 600px;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

#app .search_bar {
    position: relative;
    flex: 1 1 100%;
    display: flex;
    background-color: #ddd;
    margin: 5px 0;
    border-radius: 3px;
    font-family: Arial, Helvetica, sans-serif;
    transition: all .1s linear;
}

#app .search_bar input {
    border: unset;
    outline: unset;
    border-radius: 3px;
    padding: 5px 51px 5px 10px;
    flex: 1 1 auto;
    font-size: 16px;
    background-color: unset;
    transition: all .1s linear;
}

#app .search_bar input:hover {
    border-radius: 3px;
    box-shadow: 0 0 2px 0px #000;
}

#app .search_bar button[type="reset"] {
    position: absolute;
    right: 48px;
    top: 50%;
    transform: translateY(-50%);
    border: unset;
    background-color: unset;
    color: #0077ff;
    padding: 10px 15px;
    cursor: pointer;
    transition: all .1s linear;
}

#app .search_bar button[type="reset"]:hover {
    border-radius: 3px;
    box-shadow: 0 0 2px 0px #000;
}


#app .search_bar button[type="submit"] {
    border: unset;
    border-radius: 0 3px 3px 0;
    padding: 10px 15px;
    margin: 0;
    background: #b8b8b8;
    cursor: pointer;
    color: #0077ff;
    transition: all .1s linear;
}

#app .search_bar button[type="submit"]:hover {
    border-radius: 3px;
    box-shadow: 0 0 2px 0px #000;
}

#app .search_bar button[type="reset"] svg, #app .search_bar button[type="submit"] svg {
    margin-bottom: -3px;
}

#app .task_list {
    flex: 1 1 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding: 0;
}

#app .task_list-task {
    position: relative;
    flex: 1 1 100%;
    display: flex;
    background-color: #ddd;
    margin: 5px 0;
    border-radius: 3px;
    font-family: Arial, Helvetica, sans-serif;
    opacity: 1;
    transition: all .1s linear;
}

#app .task_list-task.checked {
    opacity: 0.7;
}

#app .task_list-task.checked:after {
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    display: block;
    width: 100%;
    border-bottom: 1px solid #797979;
}

#app .task-label {
    padding: 10px 15px;
    cursor: pointer;
    transition: all .1s linear;
}

#app .task-label:hover {
    border-radius: 3px;
    box-shadow: 0 0 2px 0px #000;
}

#app .task-data {
    flex: 1 1 auto;
    display: flex;
    align-items: center;
}

#app .task-buttons {
    display: flex;
    align-items: center;
    justify-content: center;
}

#app .buttons-remove {
    border: unset;
    border-radius: 0 3px 3px 0;
    padding: 10px 15px;
    margin: 0;
    background: #b8b8b8;
    cursor: pointer;
    color: #d62323;
    transition: all .1s linear;
}

#app .buttons-remove:hover {
    border-radius: 3px;
    box-shadow: 0 0 2px 0px #000;
}

#app .buttons-remove svg {
    margin-bottom: -3px;
}

#app .task-name_edit {
    border: unset;
    outline: unset;
    border-radius: 3px;
    padding: 5px 10px;
    margin: 0 15px;
    flex: 1 1 auto;
    font-size: 16px;
}

#app .task-name_view {
    padding: 5px 10px;
    margin: 0 15px;
    flex: 1 1 auto;
    font-size: 16px;
    min-height: 18.4px;
    cursor: text;
    transition: all .1s linear;
}

#app .task-name_view:hover {
    border-radius: 3px;
    box-shadow: 0 0 2px 0px #000;
}